"use client";

import React, { useEffect, useRef, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import StockTransferModal, {
  StockTransferFormData,
} from "./StockTransferkFormModal";
import ConfirmDeleteModal from "../Company/ConfirmDeleteModal";
import { ProductService } from "@/services/product.service";
import toast from "react-hot-toast";

/* =========================
   Types
========================= */
interface StockDto {
  id: string;
  productId: string;
  productName: string;
  branchName: string;
  quantity: number;              // ‚úÖ FIX: primitive number
  isLowStock: boolean;
  unit?: string;
}

/* =========================
   Action Menu
========================= */
function ActionMenu({
  onDelete,
  onTransfer,
}: {
  onDelete: () => void;
  onTransfer: () => void;
}) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const close = (e: MouseEvent) => {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    };
    document.addEventListener("mousedown", close);
    return () => document.removeEventListener("mousedown", close);
  }, []);

  return (
    <div ref={ref} className="relative">
      <button onClick={() => setOpen((p) => !p)}>‚ãÆ</button>

      {open && (
        <div className="absolute right-0 z-50 w-40 rounded border bg-white shadow">
          <button
            onClick={() => {
              onTransfer();
              setOpen(false);
            }}
            className="w-full px-4 py-2 text-left hover:bg-gray-100"
          >
            üîÅ Transfer
          </button>

          <button
            onClick={() => {
              onDelete();
              setOpen(false);
            }}
            className="w-full px-4 py-2 text-left text-red-600 hover:bg-red-50"
          >
            üóë Delete
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   Stock Table
========================= */
export default function StockTransferTable() {
  const [stocks, setStocks] = useState<StockDto[]>([]);
  const [search, setSearch] = useState("");

  const [openTransfer, setOpenTransfer] = useState(false);
  const [openDelete, setOpenDelete] = useState(false);

  const [selectedStock, setSelectedStock] = useState<StockDto | null>(null);
  const [loading, setLoading] = useState(false);

  /* =========================
     LOAD STOCKS
  ========================= */
  async function loadStocks() {
    try {
      setLoading(true);
      const res = await ProductService.getAllStocks();
      setStocks(res.data.data);
    } catch {
      toast.error("Failed to load stocks");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    loadStocks();
  }, []);

  /* =========================
     TRANSFER
  ========================= */
  async function handleTransfer(data: StockTransferFormData) {
    try {
      await ProductService.CreateStockTransfrerAsyn({
        productId: data.productId,
        fromBranchId: data.fromBranchId,
        toBranchId: data.toBranchId,
        quantity: Number(data.quantity), // ‚úÖ convert only here
      });

      toast.success("Stock transferred successfully");
      setOpenTransfer(false);
      loadStocks();
    } catch {
      toast.error("Transfer failed");
    }
  }

  /* =========================
     FILTER
  ========================= */
  const filteredStocks = stocks.filter(
    (s) =>
      s.productName.toLowerCase().includes(search.toLowerCase()) ||
      s.branchName.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="bg-white p-6 rounded-xl">
      {/* Header */}
      <div className="flex justify-between mb-4">
        <Input
          placeholder="Search stock..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-64"
        />
      </div>

      {/* Table */}
      <table className="w-full border-collapse">
        <thead className="bg-gray-50">
          <tr>
            <th className="px-3 py-2 text-left">Product</th>
            <th className="px-3 py-2 text-left">Branch</th>
            <th className="px-3 py-2 text-left">Qty</th>
            <th className="px-3 py-2 text-left">Status</th>
            <th className="px-3 py-2 text-left">Unit</th>
            <th className="px-3 py-2 text-right">Action</th>
          </tr>
        </thead>

        <tbody>
          {loading ? (
            <tr key="loading">
              <td colSpan={6} className="py-6 text-center">
                Loading...
              </td>
            </tr>
          ) : filteredStocks.length === 0 ? (
            <tr key="empty">
              <td colSpan={6} className="py-6 text-center text-gray-500">
                No stock found
              </td>
            </tr>
          ) : (
            filteredStocks.map((s) => (
              <tr
  key={`${s.productId}-${s.branchName}`}
  className="border-t"
>
                <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{s.productName}</td>
                <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{s.branchName}</td>
                <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{s.quantity}</td>
                <td className="x-3 text-sm text-gray-600 dark:text-gray-400">
                  {s.isLowStock ? (
                    <span className="text-red-600 x-3 text-sm ">Low</span>
                  ) : (
                    <span className="text-green-600 x-3 text-sm">Normal</span>
                  )}
                </td>
                <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{s.unit ?? "-"}</td>
                <td className="px-3 py-2 text-right">
                  <ActionMenu
                    onTransfer={() => {
                      setSelectedStock(s);
                      setOpenTransfer(true);
                    }}
                    onDelete={() => {
                      setSelectedStock(s);
                      setOpenDelete(true);
                    }}
                  />
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>

      {/* TRANSFER MODAL */}
      <StockTransferModal
        open={openTransfer}
        onClose={() => setOpenTransfer(false)}
        onSubmit={handleTransfer}
      />

      {/* DELETE MODAL */}
      <ConfirmDeleteModal
        open={openDelete}
        loading={false}
        onClose={() => setOpenDelete(false)}
        onConfirm={() => {
          toast.success("Delete not implemented yet");
          setOpenDelete(false);
        }}
      />
    </div>
  );
}
