"use client";

import { useEffect, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";
import { CompanyService } from "@/services/company.service";
import { ProductService } from "@/services/product.service";
import toast from "react-hot-toast";

/* =========================
   Types
========================= */
interface Branch {
  id: string;
  branchName: string;
}

interface Product {
  id: string;
  name: string;
}

export interface StockTransferFormData {
  productId: string;
  fromBranchId: string;
  toBranchId: string;
  quantity: Number;
}

const emptyForm: StockTransferFormData = {
  productId: "",
  fromBranchId: "",
  toBranchId: "",
  quantity: 0,
};

export default function StockTransferModal({
  open,
  onClose,
  onSubmit,
}: {
  open: boolean;
  onClose: () => void;
  onSubmit: (data: StockTransferFormData) => void;
}) {
  const [form, setForm] = useState(emptyForm);
  const [branches, setBranches] = useState<Branch[]>([]);
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!open) return;

    Promise.all([
      CompanyService.getAllBranches(),
      ProductService.getproduct(),
    ])
      .then(([b, p]) => {
        setBranches(b.data.data);
        setProducts(p.data.data);
      })
      .catch(() => toast.error("Failed to load data"));
  }, [open]);

  const update = (k: keyof StockTransferFormData, v: string) =>
    setForm((p) => ({ ...p, [k]: v }));

  const validate = () => {
    if (!form.productId) return toast.error("Product required"), false;
    if (!form.fromBranchId) return toast.error("From branch required"), false;
    if (!form.toBranchId) return toast.error("To branch required"), false;

    if (form.fromBranchId === form.toBranchId)
      return toast.error("Branches must be different"), false;

    if (!form.quantity || Number(form.quantity) <= 0)
      return toast.error("Invalid quantity"), false;

    return true;
  };

  const submit = async () => {
    if (!validate()) return;
    setLoading(true);
    await onSubmit(form);
    setLoading(false);
    setForm(emptyForm);
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div className="w-full max-w-3xl rounded-xl bg-white dark:bg-gray-900 shadow-xl">
        {/* Header */}
        <div className="relative p-5 border-b dark:border-gray-800">
          <h3 className="text-lg font-semibold text-center">Transfer Stock</h3>
          <button
            onClick={onClose}
            className="absolute right-5 top-1/2 -translate-y-1/2 text-2xl"
          >
            √ó
          </button>
        </div>

        {/* Body */}
        <div className="p-6 grid grid-cols-1 md:grid-cols-2 gap-6">
          <Field label="From Branch" required>
            <select
              className="w-full rounded-md border px-3 py-2"
              value={form.fromBranchId}
              onChange={(e) => update("fromBranchId", e.target.value)}
            >
              <option value="">Select source branch</option>
              {branches.map((b) => (
                <option key={b.id} value={b.id}>
                  {b.branchName}
                </option>
              ))}
            </select>
          </Field>

          <Field label="To Branch" required>
            <select
              className="w-full rounded-md border px-3 py-2"
              value={form.toBranchId}
              onChange={(e) => update("toBranchId", e.target.value)}
            >
              <option value="">Select destination branch</option>
              {branches.map((b) => (
                <option key={b.id} value={b.id}>
                  {b.branchName}
                </option>
              ))}
            </select>
          </Field>

          <Field label="Product" required full>
            <select
              className="w-full rounded-md border px-3 py-2"
              value={form.productId}
              onChange={(e) => update("productId", e.target.value)}
            >
              <option value="">Select product</option>
              {products.map((p) => (
                <option key={p.id} value={p.id}>
                  {p.name}
                </option>
              ))}
            </select>
          </Field>

          <Field label="Quantity" required>
            <Input
              type="number"
              value={form.quantity.toString()}
              onChange={(e) => update("quantity", e.target.value)}
            />
          </Field>
        </div>

        {/* Footer */}
        <div className="p-5 border-t flex justify-end gap-3">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={submit} disabled={loading}>
            {loading ? "Transferring..." : "Transfer"}
          </Button>
        </div>
      </div>
    </div>
  );
}

/* Helper */
function Field({
  label,
  children,
  required,
  full,
}: {
  label: string;
  children: React.ReactNode;
  required?: boolean;
  full?: boolean;
}) {
  return (
    <div className={full ? "md:col-span-2" : ""}>
      <Label>
        {label} {required && <span className="text-red-500">*</span>}
      </Label>
      {children}
    </div>
  );
}
